import * as React from 'react';
import { Button } from 'react-bootstrap';
import { BootstrapTable, TableHeaderColumn, SelectRow } from 'react-bootstrap-table';
import { AccountPicker, PlayerPicker, Spinner } from '../common';
import { BrightcovePlayerProps, getBrightcovePlayerPropsFromQueryString } from '../brightcove-player';
import { VideoSearchControls } from '../manage-videos';

import { BCPlayer, BCVideo } from '@brightcove/video-cloud-sdk';
import { ConnectorContext, VideoSearchParameters, getDefaultVideoSearchParameters, SearchTarget } from '../../services/connector';
import { Account, SPUtility } from '../../services/sharepoint';

import autobind from 'autobind-decorator';
import * as URI from 'urijs';
import { filter, get } from 'lodash';

export class EditPartVideo extends React.Component<EditPartPlayerProps, EditPartPlayerState> {
    constructor(props: EditPartPlayerProps, context?: any) {
        super(props, context);

        let searchParameters = getDefaultVideoSearchParameters();
        searchParameters.searchTarget = SearchTarget.VideoCloud;

        let bcPlayerProps = getBrightcovePlayerPropsFromQueryString();
        bcPlayerProps.mode = 'Video';
        bcPlayerProps.sharepointVersion = this.props.connectorContext.spAddinContext.SPProductNumber;

        const uri = URI();
        const info = uri.query(true) as { [key: string]: string };
        let pageServerRelativeUrl: string = '';
        if (info.pageServerRelativeUrl) {
            pageServerRelativeUrl = URI(info.pageServerRelativeUrl).normalize().pathname();
        }

        this.state = {
            playerFilter: '',
            bcPlayerProps,
            searchParameters,
            isLoadingVideos: false,
            videos: [],
            pageServerRelativeUrl
        };
    }

    componentDidMount() {
        this.searchForVideos(this.state.searchParameters);
    }

    @autobind
    async searchForVideos(searchParams: Partial<VideoSearchParameters>): Promise<void> {
        const { selectedAccount } = this.props.connectorContext;
        if (!selectedAccount || !selectedAccount.accountId) {
            return;
        }

        this.setState({
            isLoadingVideos: true
        });

        const { connectorContext } = this.props;
        const { bcApiGateway } = connectorContext;
        const videosApi = bcApiGateway.videosApi;

        const bcParams = connectorContext.getBCVideoSearchParams(searchParams);
        let videos = await videosApi.getVideos(selectedAccount.accountId, bcParams);

        if (searchParams.folderId) {
            videos = filter(videos, { 'folder_id': searchParams.folderId });
        }

        this.setState({
            isLoadingVideos: false,
            videos
        });
    }

    @autobind
    handleAccountChange(newAccount: Account) {
        const { connectorContext } = this.props;
        const { selectedAccount } = connectorContext;

        if (!selectedAccount || selectedAccount.accountId !== newAccount.accountId) {
            connectorContext.selectedAccount = newAccount;
            this.searchForVideos(this.state.searchParameters);
        }
    }

    @autobind
    handlePlayerFilterChange(value: string) {
        this.setState({
            playerFilter: value
        });
    }

    @autobind
    handlePlayerChange(selectedPlayer: BCPlayer) {
        this.setState({
            selectedPlayer
        });
    }

    @autobind
    handlePlayerEmbedCodeChanged(bcPlayerProps: Partial<BrightcovePlayerProps>) {
        this.setState({
            bcPlayerProps
        });
    }

    @autobind
    handleSearchParametersChanged(searchParameters: Partial<VideoSearchParameters>, fieldChanged: string) {
        this.setState({
            searchParameters
        });

        if (fieldChanged !== 'searchTerms') {
            this.searchForVideos(searchParameters);
        }
    }

    @autobind
    videoSelected(video: BCVideo, isSelected: boolean, e: any) {
        let selectedVideo: BCVideo | undefined;
        if (isSelected) {
            selectedVideo = video;
        }
        this.setState({
            selectedVideo
        });
    }

    @autobind
    renderVideoNameCell(cell: any, video: BCVideo, extraData: { selectedAccount: Account }) {
        const thumbnailSrc = get(video, 'images.thumbnail.src');
        let uri = URI(`${window.location.origin}${window.location.pathname}${window.location.search}`);
        uri = uri.removeQuery('mini');
        uri = uri.removeQuery('IsDlg');

        const videoUrl = `${uri.toString()}#/manage-videos/edit/${extraData.selectedAccount.accountId}/Brightcove/${video.id}`;

        return (
            <div className="row">
                <img className="col-xs-2" src={thumbnailSrc} />
                <div className="col-xs-10">
                    <a href={videoUrl} target="_blank">{video.name}</a>
                    <div className="small">{video.id}</div>
                </div>
            </div>
        );
    }

    @autobind
    async applyWebpartChanges() {
        const { connectorContext } = this.props;
        const { bcPlayerProps, selectedPlayer, selectedVideo } = this.state;

        if (!selectedVideo) {
            return;
        }

        const uri = URI();
        const info = uri.query(true) as { [key: string]: string };

        const url = info.pageServerRelativeUrl;
        const properties = {
            'accountId': connectorContext.selectedAccount ? connectorContext.selectedAccount.accountId : '',
            'playerId': !selectedPlayer ? 'default' : selectedPlayer.id,
            'modeId': selectedVideo.id || '',
            'autoplay': (bcPlayerProps.autoplay || false).toString(),
            'muted': (bcPlayerProps.muted || false).toString()
        };

        await SPUtility.updateWebPartProperties(connectorContext.spAddinContext.SPHostUrl, url, info.wpId, properties, connectorContext.spAddinContext.SPAppWebUrl);
        location.reload(true);
    }

    @autobind
    async cancelWebpartChanges() {
        location.reload(true);
    }

    render() {
        const { connectorContext } = this.props;
        const { selectedAccount } = connectorContext;

        const {
            selectedPlayer,
            playerFilter,
            searchParameters,
            isLoadingVideos,
            videos,
            selectedVideo,
            pageServerRelativeUrl
        } = this.state;

        const labelClass = 'col-xs-3 control-label small';
        const controlClass = 'col-xs-9';

        const selectRow: SelectRow = {
            mode: 'radio',
            clickToSelect: true,
            className: 'info',
            onSelect: this.videoSelected
        };

        const canApply = !isLoadingVideos && selectedAccount && selectedVideo && pageServerRelativeUrl;

        return (
            <div className="form-horizontal">
                <div className="form-group">
                    <label className={labelClass}>Brightcove Account:</label>
                    <div className={controlClass}>
                        <AccountPicker className="input-sm" availableAccounts={connectorContext.availableAccounts} selectedAccount={selectedAccount} onChange={this.handleAccountChange} />
                    </div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Player Filter:</label>
                    <div className={controlClass}>
                        <div className="input-group">
                            <input
                                type="search"
                                placeholder="Search"
                                className="form-control input-sm"
                                value={playerFilter}
                                onChange={(e) => this.handlePlayerFilterChange(e.target.value)}
                            />
                            <div className="input-group-btn searchButton">
                                <Button bsSize="small"><i className="glyphicon glyphicon-search" /></Button>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Player:</label>
                    <div className={controlClass}>
                        <PlayerPicker className="input-sm" connectorContext={connectorContext} playerType="Video" playerFilter={playerFilter} defaultPlayerId={selectedAccount && selectedAccount.defaultVideoPlayerId} selectedPlayerId={selectedPlayer && selectedPlayer.id} onChange={this.handlePlayerChange} />
                    </div>
                </div>
                <VideoSearchControls
                    connectorContext={connectorContext}
                    labelClass={labelClass}
                    controlClass={controlClass}
                    showSearchTarget={false}
                    formControlClass="input-sm small"
                    searchParameters={searchParameters}
                    onSearchParametersChanged={this.handleSearchParametersChanged}
                    onSearchForVideos={this.searchForVideos}
                />
                {isLoadingVideos && selectedAccount &&
                    <Spinner loadingText="Loading Videos..." />
                }
                {!isLoadingVideos && selectedAccount &&
                    <div className="well" style={{ height: '250px', overflow: 'auto', margin: '0 15px' }} >
                        <BootstrapTable data={videos} striped={true} bordered={false} selectRow={selectRow}>
                            <TableHeaderColumn dataField="name" dataFormat={this.renderVideoNameCell} formatExtraData={{ selectedAccount }}>Videos</TableHeaderColumn>
                            <TableHeaderColumn dataField="id" isKey={true} hidden={true}>ID</TableHeaderColumn>
                        </BootstrapTable>
                        <hr />
                    </div>
                }
                {!isLoadingVideos && selectedAccount && selectedVideo && !pageServerRelativeUrl &&
                    <div className="col-xs-12" style={{ paddingTop: '5px' }}>
                        <p>Unable to automatically set web part properties as the page url has not been specified. Please set the page server relative url and try again, or, apply the following web part properties manually:</p>
                        <p>Account Id: {connectorContext.selectedAccount && connectorContext.selectedAccount.accountId}</p>
                        <p>Player Id: {selectedPlayer && selectedPlayer.id}</p>
                        <p>Video Id: {selectedVideo && selectedVideo.id}</p>
                    </div>
                }
                <div className="col-xs-12" style={{ paddingTop: '5px' }}>
                    <Button className="pull-right" bsStyle="primary" disabled={!canApply} style={{ margin: '2px' }} onClick={this.applyWebpartChanges}>Apply</Button>
                    <Button className="pull-right" style={{ margin: '2px' }} onClick={this.cancelWebpartChanges}>Cancel</Button>
                </div>
            </div>
        );
    }
}

interface EditPartPlayerProps {
    connectorContext: ConnectorContext;
    playerProps: Partial<BrightcovePlayerProps>;
}

interface EditPartPlayerState {
    selectedPlayer?: BCPlayer;
    playerFilter: string;
    bcPlayerProps: Partial<BrightcovePlayerProps>;
    searchParameters: Partial<VideoSearchParameters>;

    isLoadingVideos: boolean;
    videos: Array<BCVideo>;
    selectedVideo?: BCVideo;
    pageServerRelativeUrl: string;
}


